import React, { useState, useEffect, useContext } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router';
import styles from "../style/SignUp.module.css"

import { ToastContext } from "../context/ToastContext";

export default function SginUp(){

  const navigate = useNavigate();
  const toast = useContext(ToastContext);
  
  const SaveSignup = async () => {
    if(checkPassword == password){
      try {
        await axios.post(
          '/users/save',
          {
            email: email,
            name: name,
            password: password
          }
        );
        
        toast.setIsShow(true);
        toast.setMessage(' .');

        // navigate('/signIn');
        navigate('/');

      } catch (error) {
        console.log(error);
        SetErrorMessage(error.response.data.message);
      }
    }
  };

  const CheckEmail = async () => {
    try {
      await axios.post(
        '/mail/sendMail',
        null,
        {
            params:
            {
                email:email
            }
        }
      ).then(response => {
        console.log(response);
        SetAuthState(true);
        SetErrorMessage(response.data.message);
      }).catch(error => {
        console.log('error',error);
        SetErrorMessage(error.response.data.message);
      });
      
    } catch (error) {
      console.log(error);
    }
  };

  const AuthMail = async () => {
    try {
      await axios.post(
        '/mail/authMail',
        null,
        {
            params:
            {
                email: email,
                auth: auth
            }
        }
      ).then(response => {
        console.log(response);
        SetAuthState(false);
        SetErrorMessage('.');
      }).catch(error => {
        console.log(error);
        SetErrorMessage(error.response.data.message);
      });
      
    } catch (error) {
      console.log(error);
    }

  }

  const [email, SetEmail] = useState("");
  const [name, SetName] = useState("");
  const [password, SetPassword] = useState("");
  const [auth, SetAuth] = useState("");
  const [errorMessage, SetErrorMessage] = useState("");

  const [authState, SetAuthState] = useState(false);
  const [checkPassword, SetCheckPassword] = useState("");

  useEffect(() => {
    if(checkPassword != password){
      SetErrorMessage("   .");
    }
    else{
      SetErrorMessage("");
    }
}, [password, checkPassword]);
  

  return (
    <>
    <div className="col-md-12">
      <div className="row">
        <div className={styles.MainCenter}>
          <div className="col-md-6">
            <div className={styles.SignUp} >
              <h1 className={styles.h1}>Health SNS </h1>
          
              <div className={styles.Center}>
                <input type="text"
                  id="email" placeholder=" " 
                  className={styles.input2}
                  onChange={(e)=>{
                    SetEmail(e.target.value);
                  }}/>
                <button className={`${styles.Button2}`} onClick={CheckEmail}></button>
              </div>

              { authState &&
              <div className={styles.Center}>
                <input type="text"
                  id="mailAuthKey" placeholder=" " 
                  className={styles.input2}
                  onChange={(e)=>{
                    SetAuth(e.target.value);
                  }}
                  />
                <button className={`${styles.Button2}`}  onClick={AuthMail}></button>
              </div>
              }

              <div className={styles.Center}>
                <input type="text"
                      id="name" placeholder=" "
                      className={styles.input}
                      onChange={(e)=>{
                        SetName(e.target.value);
                      }}
                      />
              </div>

              <div className={styles.Center}>
                <input type="password" 
                        id="password" placeholder=" ( 4 , 16  )"
                        className={`${styles.input}`}
                        onChange={(e)=>{
                          SetPassword(e.target.value);
                  }} 
                  />
              </div>

              <div className={styles.Center}>
                <input type="password"
                      placeholder="  " 
                      className={`${styles.input}`}
                      onChange={(e)=>{
                        SetCheckPassword(e.target.value);
                }}/>
              </div>
              <p className='text-danger'>{errorMessage}</p>
              <div className={`${styles.Right}`}>
                <Link to="/signIn" role="button" className={`btn btn-danger`}></Link>
                <button className={`btn btn-primary ${styles.Button}`} onClick={SaveSignup}></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    </>
  );
};